@mixin mq($type) {
    $mobile: 320px;
    $tablet: 768px;
    $desktop: 1280px;
    
    $event-section: 500px;
    $registration-section: 1075px;

    // @if $type =='mobile-fixed' {
    //     @media screen and (min-width: 480px) and (max-width: 767.98px) {
    //         @content;
    //     }
    // }
    @if $type =='mobile-only' {
        @media screen and (max-width: #{$tablet - 0.02}) {
            @content;
        }
    }

    @if $type =='mobile' {
        @media screen and (min-width: $mobile) {
            @content;
        }
    }

    @if $type =='event-section' {
        @media screen and (min-width: $event-section) {
            @content;
        }
    }

    @if $type =='registration-section' {
        @media screen and (min-width: $registration-section) {
            @content;
        }
    }

    @if $type =='tablet-only' {
        @media screen and (min-width: $tablet) and (max-width: #{$desktop - 0.02}) {
            @content;
        }
    }

    @if $type =='tablet' {
        @media screen and (min-width: $tablet) {
            @content;
        }
    }

    @if $type =='not-desktop' {
        @media screen and (max-width: #{$desktop - 0.02}) {
            @content;
        }
    }

    @if $type =='desktop' {
        @media screen and (min-width: $desktop) {
            @content;
        }
    }
}

@mixin fonts($fw, $fs, $lh, $tt: false) {
    font-weight: $fw;
    font-size: $fs;
    line-height: $lh;

    @if $tt {
        text-transform: $tt;
    }
}