:root {
  --main-text-cl: #2e2520;
  --secondary-text-cl: #000;
  --hover-text-cl: #876655;
  --button-text-cl: #fff;
  --decoration-text-cl: rgba(171, 129, 108, .5);
  --main-bg-cl: #fffff9;
  --button-bg-cl: #ab816c;
  --btn-hover-bg-cl: #826759;
  --event-bg-cl: #fef3e4;
  --transition-duration: 300ms;
  --transition-timing-function: ease-in;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  background-color: var(--main-bg-cl);
  color: var(--main-text-cl);
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
}

@media screen and (min-width: 1280px) {
  body {
    font-size: 18px;
    font-weight: 400;
    line-height: 22px;
  }
}

body, h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

.link {
