body.show-menu .backdrop {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

body.show-menu .menu {
  transform: translateX(0%);
}

.backdrop {
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
  transition: opacity .2s ease-in-out, visibility .2s ease-in-out;
}

.menu {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  background-color: rgba(254, 243, 228, .898);
  flex-direction: column;
  align-items: center;
  gap: 40px;
  padding-top: 25px;
  transition: transform .2s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-btn {
  background-color: var(--event-bg-cl);
  cursor: pointer;
  transition: color var(--transition-duration) var(--transition-timing-function);
  border: 1px solid rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.menu-btn--open {
  border-radius: 30%;
  padding: 10px;
}

.menu-btn--close {
  position: absolute;
  top: 20px;
  right: 20px;
}

@media screen and (min-width: 1280px) {
  .menu-btn {
    display: none;
  }
}

.menu-btn:hover, .menu-btn:focus {
  color: var(--btn-hover-bg-cl);
}

.burger-btn__icon {
  fill: currentColor;
  width: 30px;
  height: 16px;
}

.close__icon {
  fill: currentColor;
  width: 18px;
  height: 18px;
}

.menu__list {
  border-radius: 50%;
  flex-direction: column;
  align-items: center;
  gap: 45px;
  display: flex;
}

.menu__link {
  color: inherit;
  transition: color var(--transition-duration) var(--transition-timing-function), background-color var(--transition-duration) var(--transition-timing-function);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 30%;
  padding: 8px;
  font-size: 24px;
  font-weight: 500;
  line-height: 30px;
}

.menu__link:hover, .menu__link:focus {
  color: var(--event-bg-cl);
  background-color: var(--btn-hover-bg-cl);
}

